html{height:100%}body{height:100%;margin:0}#root{color:#4e4e4e;font-family:sans-serif;height:100%}h1,h2,h3,h4,h5{color:rgba(0,0,0,.6);font-family:sans-serif;font-weight:100}h1{text-transform:lowercase}h3,h4,h5{margin-bottom:.25rem}label{box-sizing:border-box;color:#4e4e4e;display:block;font-family:sans-serif;font-size:1rem;font-weight:100;margin:.25rem;opacity:.75}br{line-height:1.5rem}main{padding:1em 2em;min-height:100vh}main>section{margin-bottom:2em}article{background:#fff;border:0 solid #fafafa;border-radius:.25rem;box-shadow:0 10px 40px rgba(0,0,0,.1);margin:.25rem;padding:1rem 1.5rem;text-align:left}article.padded{padding:.75rem 2.5rem}article.padded>div{margin:.25rem}article.error{background:#fff6f6;border-color:#e0b4b4;color:#9f3a38}article.warning{background:#ffffe0;border-color:#eeeeae}article>ul{margin:0;padding:0}details summary{display:block;margin:.5rem .25rem;opacity:.75;outline:0;overflow:hidden;text-align:left;text-overflow:ellipsis;vertical-align:middle;white-space:nowrap}details summary+div{margin-top:.75rem}header,summary{margin-bottom:2em;text-align:center}summary{align-items:stretch;display:flex;justify-content:space-between;flex-wrap:wrap}summary article{color:rgba(0,0,0,.6);flex:0 1 auto;text-align:left}summary>section{display:flex;flex:0 1 auto;text-align:left}